/* src/pages/Home.css */

.home-container {
  font-family: 'Arial', sans-serif;
  padding: 20px;
  background-color: #f4f4f9;
}

.hero-section {
  background-color: #282c34;
  color: white;
  padding: 50px 0;
  text-align: center;
}

.profil-pic {
  border-radius: 50%;
  width: 150px;
  height: 150px;
  object-fit: cover;
  margin-bottom: 20px;
}

.hero-section h1 {
  font-size: 3em;
  margin: 20px 0;
}

.hero-section p {
  font-size: 1.2em;
  margin-bottom: 30px;
}

.cta-btn {
  padding: 10px 20px;
  font-size: 1em;
  background-color: #f39c12;
  border: none;
  color: white;
  cursor: pointer;
  border-radius: 5px;
}

.cta-btn:hover {
  background-color: #e67e22;
}

.about-section, .projects-section {
  margin-top: 50px;
  text-align: center;
}

.about-section h2, .projects-section h2 {
  font-size: 2.5em;
  color: #333;
  margin-bottom: 20px;
}

.project-cards {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  gap: 20px;
}

.project-card {
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  width: 250px;
  text-align: center;
  padding: 20px;
}

.project-card img {
  width: 100%;
  height: 150px;
  object-fit: cover;
}

.project-card h3 {
  font-size: 1.5em;
  margin-top: 10px;
}

.project-card p {
  font-size: 1em;
  margin-top: 10px;
}

.footer {
  text-align: center;
  padding: 20px;
  background-color: #333;
  color: white;
}

.footer a {
  color: #f39c12;
  text-decoration: none;
  margin: 0 10px;
}

.footer a:hover {
  text-decoration: underline;
}
